img {
    display: block;
    width: 100%;
    height: auto;
}

.about__cow {
display: block;
margin: 0 auto;

@media screen and (min-width: 768px) {
    min-width: 336px;
    }
@media screen and (min-width: 1280px) {
    min-width: 501px;
    }
}
.about {
    background-color: #FFF5F6;
@media screen and (max-width: 767px) {
    background-image: url('../images/about-mob-2.png');
  
    background-repeat: no-repeat;
    background-position: 20% 80%;
    @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
     background-image: url('../images/about-mob-2-2x.png');
     background-size: 100%;
    }
}
@media screen and (min-width: 1280px) {
    background-image: url(../images/about-2.png);
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: bottom;
    @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
     background-image: url('../images/about-2-2x.png');
     background-size: 100%;
    }
}

   

}

.about__first-tittle {
    display: block;
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.17;
    text-align: center;
    text-transform: uppercase;
    color: #FFA5BA;
    padding-top: 40px;
    margin-bottom: 15px;
@media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.15;
    padding-top: 20px;
    margin-bottom: 16px;
}
@media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.13;
    padding-top: 60px;
}
}

.about__tittle {
    font-family: 'Titan One';
    font-weight: 400;
    font-size: 34px;
    line-height: 1.15;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    color: #D41443;
    margin-bottom: 32px;
   
    @media screen and (min-width: 768px) {
    font-size: 48px;
    margin-bottom: 40px;
}
    @media screen and (min-width: 1280px) {
    font-size: 58px;
    line-height: 1.14;
    margin-bottom: 60px;
}
}

.about__description {
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.58;
    
    color: #000000;
    margin-top: 40px;
@media screen and (min-width: 768px) {
        font-size: 14px;
        line-height: 1.79;
    }
@media screen and (min-width: 1280px) {
        font-size: 16px;
        line-height: 1.75;
        letter-spacing: 0.04em;
    }
}

.about__icecream {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.58;

    color: #907E82;

    margin-top: 40px;
    margin-bottom: 20px;
@media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.79;
    margin-bottom: 40px;
}
    @media screen and (min-width: 1280px) {
    font-size: 14px;
    line-height: 1.93;
    
    margin-top: 32px;
}
}

.about__button {
    width: 176px;
    height: 44px;
    left: 20px;
    top: 3412px;

    background: #FFFFFF;
    border-radius: 22px;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.29;
    display: block;
    align-items: center;
    text-align: center;
    cursor: pointer;
    border-color: transparent;
    color: #D41443;
    transition: background-color 280ms cubic-bezier(0.4, 0, 0.2, 1);
@media screen and (min-width: 768px) {
    font-size: 16px;
    line-height: 1.31;
  
    }      
&:focus,
&:hover {
    background-color: #ecebeb;
  }
}

.about__tab {
@media screen and (min-width: 768px) {    
display: flex;  
align-items: center;
justify-content: center; 
}
}

.text__tab {
@media screen and (min-width: 768px) {    
    margin-left: 32px;
    }
@media screen and (min-width: 1280px) {
    margin-left: 139px;
}
}

.about__button__icon {
    margin-left: 12px;
    stroke: #D41443;
  }